import ArrowDropDown from '@/assets/icons/arrow-dropdown.svg';

export default {
  name: 'Select',
  components: {
    ArrowDropDown,
  },
  model: {
    prop: 'modelValue',
    event: 'update:modelValue',
  },
  props: {
    selectItems: {
      type: Array, // [ { text: 'text value' value: 10 } ]
      required: true,
    },
    modelValue: {
      required: true,
      type: String,
    },
    inputLabel: String,
  },

  data: () => ({
    isInputFocused: false,
    isLabelActive: false,
    isItemsActive: false,

    localSelectModel: '',
    localSelectItems: [],

    scrollbarOptions: {
      maxScrollbarLength: 30,
    },
  }),

  mounted() {
    if (this.modelValue.length > 0) {
      let value = this.selectItems.find((item) => item === this.modelValue);

      this.localSelectModel = value === undefined ? this.modelValue : value;
    }

    this.localSelectItems = [...this.selectItems];
  },

  methods: {
    handleInputFocus() {
      if (this.isItemsActive) {
        // Close item list
        this.handleInputBlur();
      } else {
        // Filter select items
        this.filterLocalSelectItems();

        // Open item list
        this.isInputFocused = true;
        this.isLabelActive = true;
        this.isItemsActive = true;
      }
    },
    handleInputBlur() {
      if (this.isInputFocused) this.isInputFocused = false;

      setTimeout(() => {
        if (this.modelValue.length === 0) {
          this.isLabelActive = false;
        }
      }, 0);

      setTimeout(() => {
        this.resetLocalSelectItems();
        this.isItemsActive = false;
      }, 110);
    },
    filterLocalSelectItems() {
      this.localSelectItems.splice(
        this.localSelectItems.findIndex(
          (item) => item === this.localSelectModel
        ),
        1
      );
    },
    resetLocalSelectItems() {
      this.localSelectItems = [...this.selectItems];
    },
    handleSelectItem(selectedItem) {
      this.localSelectModel = selectedItem;

      // Close items
      this.handleInputBlur();

      // Update parent model
      this.$emit('update:modelValue', this.localSelectModel);
    },
    isItemSelected(val) {
      return this.modelValue === val;
    },
  },
};
