import InfoIcon from '@/assets/icons/Icon metro-info.svg';

export default {
  name: 'CalculatorChart',
  components: {
    InfoIcon,
  },
  props: {
    items: Array,
    ranges: Array,
  },
  methods: {
    calcWidth(progress) {
      const MAX_PROGRESS = 450; // px
      return `${(progress / 100) * MAX_PROGRESS}px`;
    },
  },
};
